import { Component, ViewChild } from '@angular/core';
import { DatePipe } from '@angular/common';
import { IonicPage, NavController, NavParams, Events, LoadingController, Loading, AlertController, ModalController, ViewController } from 'ionic-angular';
import { ServicesServiceProvider } from '../../providers/services-service/services-service';
import * as moment from 'moment';

// models
import { Sensors } from '../../shared/models/sensors';
import { Features } from '../../shared/models/features';
import { Hardware } from '../../shared/models/hardware';
import { Plant } from '../../shared/models/plant';

import { GraphProvider } from '../../providers/graph/graph';
import { PlantSettingsPage } from '../plant-settings/plant-settings';

@IonicPage()
@Component({
  selector: 'page-detail',
  templateUrl: 'detail.html',
})
export class DetailPage {
  loading: Loading;

  @ViewChild('airTemperatureCanvas') airTemperatureCanvas;

  graphs:any;
  graphRange = {
    start: null,
    end: null
  }
  graphPolling;
  graphSelectedRange: number = 1;
  graphSelectedSensor: string = '';
  graphSelectedIcon: string = '';
  graphIcons = {
    'soil-temperature' : 'ios-thermometer-outline',
    'soil-moisture' : 'md-rainy',
    'soil-ph' : 'ios-water-outline',
    'soil-nutrients' : 'ios-nutrition-outline'
  }

  features: Features = new Features();
  sensors: Sensors = new Sensors();
  hardware: Hardware = new Hardware();
  plant: Plant = new Plant();

  constructor(
    public modalCtrl: ModalController,
    public navCtrl: NavController,
    public navParams: NavParams,
    public events: Events,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    public services: ServicesServiceProvider,
    private graph: GraphProvider
  ) {
    events.subscribe('state:feature', result => this.features = result.data);
    events.subscribe('state:hardware', result => this.hardware = result.data);
    events.subscribe('state:sensors', result => this.sensors = result.data);
    events.subscribe('plant:state', result => this.plant = result.data.filter(item => item.plant_id == navParams.get('id'))[0]);
    this.showLoading();
    Promise.all([
      new Promise((resolve, reject) => {
        this.services.getService('plantService')
          .getPlantData(navParams.get('id')).subscribe(resolve, reject);
      }),
      this.services.getService('stateService').getSensorsData()
    ]).then((result: Array<any>) => {
      this.plant = (result[0]||{}).data;
      this.sensors = result[1]
      // start initial graph
      this.graphSelectedSensor = 'soil-moisture-' + this.plant.plant_id;
      this.graphSelectedIcon = 'soil-moisture';
      this.plotBySensor(this.graphSelectedSensor);
    }).catch(error => this.showError(error))
  }

  presentSettingsModal(plant:number) {
    let modal = this.modalCtrl.create(PlantSettingsPage, {id:plant});
    modal.present();
  }

  plotBySensor(sensor:string): void {
    clearInterval(this.graphPolling);

    this.graphSelectedSensor = sensor;
    this.graphSelectedIcon = this.graphSelectedSensor.replace(/\-[0-9]+$/,'');

    const d = new Date();
    this.graphRange.start = d.setHours(d.getHours() - this.graphSelectedRange);
    this.graphRange.end = new Date();

    this.plot({
      id: sensor,
      sensor: sensor,
      area: this.airTemperatureCanvas.nativeElement,
      range: {
        start: this.graphRange.start,
        end: this.graphRange.end
      }
    });
  }

  plot(settings:any = {}, polling = true) {
    if(this.graphs) {
      this.graphs.destroy();
      this.graphs = null
    }
    const diff = moment(settings.range.end).diff(moment(settings.range.start), 'hours');
    const xAxesSettings = {
      timeScope: 'minute',
      stepSize: 10
    };
    const labelString = {
      'soil-temperature' : {
        label: '%d°'
      },
      'soil-moisture' : {
        label: '%d%'
      },
      'soil-ph' : {
        label: '%d'
      },
      'soil-nutrients' : {
        label: '%d',
        scaleLabel: 'ppm'
      }
    }

    if(diff > 1) {
      xAxesSettings.timeScope = 'hour';
      xAxesSettings.stepSize = 1;
    }

    if(diff > 6) {
      xAxesSettings.stepSize = Math.round(diff / 6);
    }

    const datePipe = new DatePipe('en-US');
    const before = new Date();
    this.services.getService('stateService').getSensorsHistory({
      start: datePipe.transform(settings.range.start, 'yyyy-MM-dd H:mm:ss'),
      end: datePipe.transform(settings.range.end, 'yyyy-MM-dd H:mm:ss'),
      sensor: settings.sensor
    }).then(result => {
      let min = parseInt(result.data.map(v => v.value).reduce(function(a, b) {
        return Math.min(a, b);
      }));
      min = min > 100 ? min - 40 : min - 4;
      let max = parseInt(result.data.map(v => v.value).reduce(function(a, b) {
        return Math.max(a, b);
      }));
      max = max > 100 ? max + 40 : max + 4;
      this.graphs = this.graph.plotLine(settings.area, {
        title: settings.title,
        labels: result.data.map(v => v.created_at['$date']),
        data: result.data.map(v => v.value),
        xAxes: xAxesSettings,
        yAxes: {
          min: min,
          max: max,
          beginAtZero: true,
          labelString: (labelString[settings.sensor.replace(/\-[0-9]+$/,'')]||{}).label || '%d',
          scaleLabel: (labelString[settings.sensor.replace(/\-[0-9]+$/,'')]||{}).scaleLabel
        }
      });

      if(!polling) {
        return;
      }

      this.graphPolling = setInterval(() => {
        const labels = this.graphs.data.labels;
        const values = this.graphs.data.datasets[0].data;
        const last = labels[labels.length - 1];

        this.services.getService('stateService').getSensorsHistory({
          start: datePipe.transform(new Date(last), 'yyyy-MM-dd H:mm:ss'),
          end: datePipe.transform(new Date(), 'yyyy-MM-dd H:mm:ss'),
          sensor: settings.sensor
        }).then(res => {
          const filteredData = this.graph.prepareData(xAxesSettings.timeScope, {
            labels: res.data.map(item => item.created_at['$date']),
            data: res.data.map(item => item.value)
          });

          filteredData.data.forEach((el, index) => {
            if(!labels.includes(filteredData.labels[index])) {
              labels.splice(0, 1);
              values.splice(0, 1);
              labels.push(filteredData.labels[index]);
              values.push(el);
            }
          });

          this.graphs.update();
        }).catch(err => {});
      }, 5000)
    }).catch(err => {});
  }

  graphSelectRange(interval:number) {
    this.graphSelectedRange = interval;
    this.plotBySensor(this.graphSelectedSensor);
  }

  ionViewWillEnter() {
  }

  showLoading(message:string = ''): void {
    this.loading = this.loadingCtrl.create({
      content: message || 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

  showError(error) {
    this.loading.dismiss();

    let message = error.statusText;
    let response;
    try {
      response = JSON.parse(error._body);
    }catch(e) {}

    const alert = this.alertCtrl.create({
      title: error.status == 401 ? error.statusText : (error.title ? error.title : 'Fail'),
      subTitle: response ? response.message : message,
      buttons: ['OK']
    });
    alert.present();
  }

}



