import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events, AlertController, LoadingController, Loading } from 'ionic-angular';

import { DetailPage } from '../detail/detail';

// models
import { Plant } from '../../shared/models/plant';

// services
import { ServicesServiceProvider } from '../../providers/services-service/services-service';

@IonicPage()
@Component({
  selector: 'page-plants',
  templateUrl: 'plants.html',
})
export class PlantsPage {

  plant: Array<Plant> = [];
  loading: Loading;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    events: Events,
    private services: ServicesServiceProvider,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController
  ) {
    events.subscribe('plant:state', result => this.plant = result.data);
  }

  ionViewWillEnter(): void {
    if(this.plant.length > 0) {
      return;
    }
    this.showLoading();
    this.services.getService('plantService').getPlantsData()
      .then(plants => {
        this.loading.dismiss();
        this.plant = plants;
      }).catch(err => {
        const alert = this.alertCtrl.create({
          title:  'Error fetching data',
          subTitle: 'Device data could not be retrieved. Please try again.',
          buttons: ['OK']
        });
        alert.present();
      });
  }

  goToDetail(id:number): void {
    this.navCtrl.push(DetailPage, {id});
  }

  showLoading(message:string = ''): void {
    this.loading = this.loadingCtrl.create({
      content: message || 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

}



