import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ItemSliding, ViewController, ModalController, LoadingController, Loading, AlertController } from 'ionic-angular';
import { FormBuilder, FormArray, FormGroup, Validators } from '@angular/forms';
import { PlantPeriodsFormPage } from '../plant-periods-form/plant-periods-form';
import { ServicesServiceProvider } from '../../providers/services-service/services-service';

@IonicPage()
@Component({
  selector: 'page-plant-settings',
  templateUrl: 'plant-settings.html',
})
export class PlantSettingsPage {
  parameter: string = 'cycle';
  formGeneral: FormGroup;
  formClimate: FormGroup;
  loading: Loading;
  id: string;
  data = {
    name: null,
    cycle: null,
    period_label: {items:[]}
  };

  rangeTypes = {
    'air-temperature' : {
      icon: 'ios-thermometer-outline',
      min: 0, max: 100
    },
    'air-humidity' : {
      icon: 'ios-water-outline',
      min: 0, max: 100
    },
    'light' : {
      icon: 'md-bulb',
      min: 0, max: 100
    },
    'lumens' : {
      icon: 'md-bulb',
      min: 0, max: 30000
    },
    'uv' : {
      icon: 'ios-flash-outline',
      min: 0, max: 10
    },
    'co2' : {
      icon: 'ios-cloud-outline',
      min: 0, max: 20000
    }
  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private formBuilder: FormBuilder,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    public modalCtrl: ModalController,
    private services: ServicesServiceProvider
  ) {
    this.formGeneral = this.formBuilder.group({
      name: [this.data.name, [
        Validators.required,
        Validators.maxLength(60),
        Validators.minLength(2)
      ]],
      cycle: [this.data.cycle, [
        Validators.required
      ]],
      period_label: this.formBuilder.array([])
    });
    this.formGeneral.get('cycle').valueChanges.subscribe(v => {
      if(v) {
        this.formGeneral.get('cycle').setValue(+v, {emitEvent: false});
      }
    });

    for(let i = 1; i <= 8; i++) {
      this.rangeTypes['soil-moisture-' + i] = {
        icon: 'md-rainy',
        min: 0, max: 100
      }
      this.rangeTypes['soil-temperature-' + i] = {
        icon: 'ios-thermometer-outline',
        min: 0, max: 100
      }
      this.rangeTypes['soil-nutrients-' + i] = {
        icon: 'ios-nutrition-outline',
        min: 0, max: 20000
      }
      this.rangeTypes['soil-ph-' + i] = {
        icon: 'ios-water-outline',
        min: 0, max: 10
      }
    }
  }

  ionViewDidLoad() {
    // get the plant data
    this.services.getService('plantService').getPlantData(1).subscribe(response => {
      this.setGeralForm(response.data);
    })
  }

  initTrigger() {
    const form = this.formBuilder.group({
      target: ['', Validators.required],
      description: [''],
      trigger_parameters: this.formBuilder.group({
        trigger: ['', Validators.required],
        trigger_type: ['', Validators.required],
        value: ['', Validators.required],
        comparison_type_value: ['>=', Validators.required],
        range_value: this.formBuilder.group({
          min_value: ['', Validators.required],
          max_value: ['', Validators.required]
        }),
        week: this.formBuilder.group({
          monday: this.formBuilder.group({
            time_interval: ['', Validators.required],
            schedules: new FormArray([])
          }),
          tuesday: this.formBuilder.group({
            time_interval: ['', Validators.required],
            schedules: new FormArray([])
          }),
          wednesday: this.formBuilder.group({
            time_interval: ['', Validators.required],
            schedules: new FormArray([])
          }),
          thursday: this.formBuilder.group({
            time_interval: ['', Validators.required],
            schedules: new FormArray([])
          }),
          friday: this.formBuilder.group({
            time_interval: ['', Validators.required],
            schedules: new FormArray([])
          }),
          saturday: this.formBuilder.group({
            time_interval: ['', Validators.required],
            schedules: new FormArray([])
          }),
          sunday: this.formBuilder.group({
            time_interval: ['', Validators.required],
            schedules: new FormArray([])
          })
        })
      }),
      target_parameters: this.formBuilder.group({
        target_type: ['ml', Validators.required],
        time: [100, Validators.required],
        max_execution_time: [150000, Validators.required]
      })
    });

    form.controls.trigger_parameters.get('comparison_type_value').setValue('>=');
    return form;
  }

  addTrigger(): void {
    const control = <FormArray>this.formGeneral.controls['triggers'];
    control.push(this.initTrigger());
  }

  resetIrrigationFields(): void {
    const control = <FormArray>this.formGeneral.controls['triggers'];
    for(const item of control.controls) {
      item.get('trigger_parameters.value').setValue('');
      item.get('trigger_parameters.range_value.min_value').setValue('');
      item.get('trigger_parameters.range_value.max_value').setValue('');
      item.get('target_parameters.time').setValue(100);
    }
  }

  setGeralForm(data):void {
    const control = <FormArray>this.formGeneral.controls['period_label'];
    Object.assign(this.data, data);
    for(const period of data.period_label.items) {
      control.push(this.formBuilder.group({
        start: [period.start.toString(), Validators.required],
        end: [period.end.toString(), Validators.required],
        label: [period.label, Validators.required]
      }));
    }
  }

  removePeriod(slidingItem: ItemSliding, i: number): void {
    const control = <FormArray>this.formGeneral.controls['period_label'];
    control.removeAt(i);
    slidingItem.close();
  }

  openPeriodsForm() {
    let control = <FormArray>this.formGeneral.controls['period_label'];
    const modal = this.modalCtrl.create(PlantPeriodsFormPage, {
      periods: control.value,
      cycle: this.formGeneral.get('cycle').value
    });
    modal.present();
    modal.onDidDismiss(data => {
      if(!data) return;

      let periods = control.value;
      periods.push(data);

      periods = periods.sort(function(a,b) {
        a.end = typeof a.end === 'number' ? a.end : parseInt(a.end);
        b.end = typeof b.end === 'number' ? b.end : parseInt(b.end);
        return (a.end > b.end) ? 1 : ((b.end > a.end) ? -1 : 0);
      }).map(item => {
        item.start = item.start.toString();
        item.end = item.end.toString();
        return item;
      });

      while (control.length !== 0) {
        control.removeAt(0);
      }

      periods.forEach(period => {
        control.push(this.formBuilder.group({
          start: [period.start.toString() || '', Validators.required],
          end: [period.end.toString() || '', Validators.required],
          label: [period.label || '', Validators.required]
        }));
      });
    });
  }

  checkCycle() {
    const cycle = this.formGeneral.get('cycle').value;
    const periods = this.formGeneral.get('period_label').value;
    const inRange = (x, min, max) => {
      return ((x-min)*(x-max) <= 0);
    }

    let outOfPeriod = false;
    const allowedPeriods = [];
    let i = 0;
    for(const period of periods) {
      if(period.start > cycle || period.end > cycle) {
        outOfPeriod = true;
      }else {
        allowedPeriods.push(period);
      }
      i++;
    }

    if(outOfPeriod) {
      const alert = this.alertCtrl.create({
        title: 'There are labels for periods outside the configured cycle. Do you want to remove them?',
        subTitle: '',
        buttons: [{
          text: 'Remove',
          handler: () => {
            this.formGeneral.controls['period_label'] = this.formBuilder.array([]);
            const control = <FormArray>this.formGeneral.controls['period_label'];
            for(const item of allowedPeriods) {
              control.push(this.formBuilder.group({
                start: [item.start.toString(), Validators.required],
                end: [item.end.toString(), Validators.required],
                label: [item.label, Validators.required]
              }));
            }
          }
        },{
          text: 'Cancel',
          role: 'cancel',
          handler: () => this.formGeneral.get('cycle').setValue('')
        }]
      });
      alert.present();
    }
  }

  submitGeneralForm(): void {
    this.showLoading('Please wait, saving the data...');
    const value = Object.assign({}, this.formGeneral.value);
    value.period_label = {
      items: value.period_label.map(item => {
        item.start = parseInt(item.start);
        item.end = parseInt(item.end);
        return item;
      })
    }

    this.services.getService('plantService').postPlantsData(value).then(response => {
      this.loading.dismiss();
    }, error => this.showError(error));
  }

  showLoading(message: string): void {
    this.loading = this.loadingCtrl.create({
      content: message || 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

  showError(error) {
    this.loading.dismiss();

    let message = error.statusText;
    if(error.status == 401) {
      message = 'Invalid username or password'
    }

    let response = {message: 'The service is unavailable'};
    try {
      response = JSON.parse(error._body);
    }catch(e) {}

    const alert = this.alertCtrl.create({
      title: error.status == 401 ? error.statusText : (error.title ? error.title : 'Fail'),
      subTitle: response ? response.message : message,
      buttons: ['OK']
    });
    alert.present();
  }
}



